"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Button } from "@/components/ui/button"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { mockClasses, mockStudents } from "@/lib/mock-data"
import type { AttendanceEntry } from "@/lib/types"
import { useToast } from "@/hooks/use-toast"

export default function AttendancePage() {
  const [selectedClass, setSelectedClass] = useState<string>("")
  const [selectedDate, setSelectedDate] = useState<string>(new Date().toISOString().split("T")[0])
  const [attendance, setAttendance] = useState<AttendanceEntry[]>([])
  const { toast } = useToast()

  const loadAttendance = () => {
    if (!selectedClass) return

    const studentsInClass = mockStudents.filter((s) => s.className === selectedClass)
    const attendanceEntries: AttendanceEntry[] = studentsInClass.map((student) => ({
      studentId: student.id,
      studentName: student.name,
      status: "Present",
    }))

    setAttendance(attendanceEntries)
  }

  const toggleAttendance = (studentId: string) => {
    setAttendance((prev) =>
      prev.map((entry) =>
        entry.studentId === studentId ? { ...entry, status: entry.status === "Present" ? "Absent" : "Present" } : entry,
      ),
    )
  }

  const handleSaveAttendance = () => {
    if (attendance.length === 0) {
      toast({
        title: "Error",
        description: "Please select a class and load students first",
        variant: "destructive",
      })
      return
    }

    // Simulate saving attendance
    const presentCount = attendance.filter((a) => a.status === "Present").length
    const totalCount = attendance.length
    const percentage = Math.round((presentCount / totalCount) * 100)

    toast({
      title: "Attendance Saved",
      description: `${presentCount} of ${totalCount} students present (${percentage}%)`,
    })
  }

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold tracking-tight text-foreground">Attendance</h2>
        <p className="mt-2 text-muted-foreground">Mark student attendance for classes</p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Select Class and Date</CardTitle>
          <CardDescription>Choose a class and date to mark attendance</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid gap-4 md:grid-cols-3">
            <div className="space-y-2">
              <Label htmlFor="class-select">Class</Label>
              <Select value={selectedClass} onValueChange={setSelectedClass}>
                <SelectTrigger id="class-select">
                  <SelectValue placeholder="Select a class" />
                </SelectTrigger>
                <SelectContent>
                  {mockClasses.map((classItem) => (
                    <SelectItem key={classItem.id} value={classItem.name}>
                      {classItem.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="date-select">Date</Label>
              <input
                id="date-select"
                type="date"
                className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                value={selectedDate}
                onChange={(e) => setSelectedDate(e.target.value)}
              />
            </div>

            <div className="flex items-end">
              <Button onClick={loadAttendance} disabled={!selectedClass} className="w-full">
                Load Students
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {attendance.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Mark Attendance</CardTitle>
            <CardDescription>Click on status to toggle between Present and Absent</CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Student Name</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {attendance.map((entry) => (
                  <TableRow key={entry.studentId}>
                    <TableCell className="font-medium">{entry.studentName}</TableCell>
                    <TableCell>
                      <Button
                        variant={entry.status === "Present" ? "default" : "destructive"}
                        size="sm"
                        onClick={() => toggleAttendance(entry.studentId)}
                      >
                        {entry.status}
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>

            <div className="mt-6 flex justify-end">
              <Button onClick={handleSaveAttendance} size="lg">
                Save Attendance
              </Button>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
